import { CardContent, Container, Grid, Typography } from "@mui/material";
import React from "react";
import MainTitle from "../Texts/MainTitle";
import { Card, CardHeader, Avatar, CardMedia } from "@mui/material";

import { MenuData } from "./MenuData";
import "./ThirdSection.css";


const imClicked = (nameDish) =>{
    alert(nameDish)
}

const MyDishes = () => {
  return (
    <Grid
      container
      rowSpacing={5}
      columnSpacing={3}
      alignItems="center"
      justifyContent="center"
      sx={{
        marginBottom: "4em",
      }}
    >
      {MenuData.map((data) => {
        return (
          <React.StrictMode  key={data.id}>
            <Grid item xs={12} md={4}>
              <Card onClick={()=> imClicked(data.title)} sx={{ maxWidth: '100%', background: "#1E1E1E", cursor:'pointer' }}>
                <CardHeader sx={{color:'white'}}
                  avatar={
                    <Avatar
                      sx={{ background: "var(--primary)" }}
                      aria-label="recipe"
                    >
                      {data.title.substring(0,2).toUpperCase()}
                    </Avatar>
                  }
                  title={data.title}
                  subheader={<Typography variant="caption" sx={{color:'#BABABA'}}>{data.date}</Typography>}
                />
                <CardMedia
                    component="img"
                    height="194"
                    image={data.img}
                    alt={data.title}
                />
                <CardContent>
                    <Typography variant="body2" color="#BABABA">{data.description}</Typography>
                </CardContent>
              </Card>
            </Grid>
          </React.StrictMode>
        );
      })}
    </Grid>
  );
};

const ThirdSection = () => {
  return (
    <div className="third-section">
      <Container>
        <MainTitle
          textContent="Menu"
          description="Lorem ipsum dolor sit amet, consectetur adipiscing elit. Praesent eget suscipit diam. Suspendisse iaculis tellus consequat neque feugiat, nec efficitur sem tempus. Morbi rhoncus erat nisl. Nunc fermentum enim nunc, a volutpat orci sollicitudin lacinia. Morbi porta dignissim est quis."
        />
        <MyDishes />
      </Container>
    </div>
  );
};

export default ThirdSection;
