import React from "react";
import "./FirstSection.css";
import "./Carousel.css";
import { Carousel } from "react-carousel-minimal";
import { CarouselData } from "./CarouselData";
import { Container, Typography } from "@mui/material";
import { Grid } from "@mui/material";
import MainButton from "../MainButton";
import BookIcon from '@mui/icons-material/Book';

import "react-responsive-carousel/lib/styles/carousel.min.css";
import { Carousel as ReactCarousel } from "react-responsive-carousel";

import imgRecomendados from "../../assets/images/menuFinal.PNG";
import MainTitle from "../Texts/MainTitle";
import {EventsData} from "./eventsData";
import { TestimonyData } from "./TestimonyData";
import Gridx2 from "../Gridx2";


const FirstSection = () => {
  const captionStyle = {
    fontSize: "3em",
    marginBottom:'1.5em'
  };

  const NuestraPropuesta = () => {
    

    return (
      <>
        <MainTitle
          textContent="Nuestra propuesta"
          description="Lorem ipsum dolor sit amet, consectetur adipiscing elit. Integer vel auctor elit. Praesent non est libero. Donec vel orci nec ex egestas varius. Nam non tellus ut mauris finibus malesuada sed ut nunc.amet consectetur adipisicing elit. Nostrum adipisci unde assumenda in odio facilis, distinctio autem accusantium libero quas consequatur commodi.
          debitis blanditiisre"
        />
        <Gridx2
          
          img={require("../../assets/images/foto4@2x.png").default}
          myHighLight="Lorem mensu mads peloas "
          description={`Saepe earum eveniet vero nulla, consequuntur illo, sed ratione
          debitis blanditiis eius nisi assumenda? Inventore, illo nemo quasi
          vel minus a? In?. Consequatur nemo nam vel! In quibusdam porro amet
          ex iste possimus assumenda quasi a laboriosam? Necessitatibus
          molestiae officiis vel delectus praesentium recusandae optio libero
          odio aliquam, consequuntur cum enim blanditiis.`}
        />
      </>
    );
  };

  const RecomendadoChef = () => {
    return (
      <>
        <MainTitle
          textContent="Los recomendados del chef"
          description="Lorem ipsum dolor sit amet consectetur adipisicing elit. Nostrum adipisci unde assumenda in odio facilis, distinctio autem accusantium libero quas consequatur commodi.
          debitis blanditiis eius nisi assumenda? Inventore, illo nemo quasi
              vel minus a? In? Consequatur nemo nam vel! In quibusdam porro amet
              ex iste
          "
        />
        <Grid container rowSpacing={5} columnSpacing={3}>
          <Grid item xs={12} md={12}>
            <img loading="lazy" className="imgRecomendados" src={imgRecomendados} alt="lol" />
            <MainButton 
            buttonName="Hacer pedido"
            icon={<BookIcon/>}
             />
          </Grid>
        </Grid>
      </>
    );
  };

  const Eventos = () => {
      return (
          <>
          <MainTitle
            textContent="Organizamos tus eventos"
            description="Lorem ipsum dolor sit amet consectetur adipisicing elit. Nostrum adipisci unde assumenda in odio facilis, distinctio autem accusantium libero quas consequatur commodi.
            debitis blanditiis "
          />
          <Grid container rowSpacing={5} columnSpacing={3} sx={{marginBottom:"10em"}}>
              {EventsData.map((data, index)=>{
                  return(
                      <React.StrictMode key={index}>
                      <Grid item xs={12} md={3} sx={{marginTop:'4.3em'}}>
                          <Typography sx={{fontSize:'4.5em',textAlign:'center', paddingBottom:'0.3em'}}>
                              {data.icon}
                          </Typography>
                          <Typography variant="h6" sx={{textAlign:'center'}}>
                              {data.title}
                          </Typography>
                          <Typography variant="body2" sx={{marginTop: "2.5em", textAlign:'center', color:'#94A0AB'}}>
                              {data.description}
                          </Typography>
                      </Grid>
                      </React.StrictMode>

                  )
              })}
          </Grid>
          </>
      )
  }

  const Testimonio = () =>{
      return(
          <>

            <ReactCarousel
                    showArrows={true}
                    infiniteLoop={true}
                    showThumbs={false}
                    showStatus={false}
                    autoPlay={true}
                    interval={6100}
            >
                {TestimonyData.map(data=>{
                  return(
                    <div className="myCarousel" key={data.id}>
                      <img loading="lazy" className="imgPattern" alt={data.name} src={require('../../assets/images/patterns.png').default}/>
                      <div className="containerOfElements">
                      <img loading="lazy" className="guysTestimonial" src={data.img} alt="sas"/>
                      <Typography variant={"h5"} sx={{marginTop:'0.4rem',letterSpacing:'2px',fontWeight:450, color:'white'}}>
                        {data.name}
                      </Typography>
                      <Typography variant={"h6"} sx={{fontWeight:'lighter',letterSpacing:'2px',color:'white'}}>
                        {data.client}
                      </Typography>
                      <Typography variant={"body2"} sx={{marginTop:'0.1rem',fontStyle:'italic', color:'text.secondary'}}>
                        <q className="quotes">{data.testimony}</q>
                      </Typography>
                      </div>
                    </div>
                  )
                })}
            </ReactCarousel>    
          </>
      )
  }


  return (
    <div className="firstSection">
      <Carousel
        data={CarouselData}
        time={4000}
        width="100%"
        height="100vh"
        captionStyle={captionStyle}
        radius="0"
        slideNumber={false}
        captionPosition="bottom"
        automatic={true}
        dots={true}
        pauseIconColor="white"
        pauseIconSize="5rem"
        slideBackgroundColor="darkgrey"
        slideImageFit="cover"
        thumbnails={false}
        showNavBtn={false}
        style={{
          textAlign: "center",
          maxWidth: "100%",
          maxHeight: "auto",
          borderRadius: 0,
        }}
      />
      <Container>
        <NuestraPropuesta />
        <RecomendadoChef />
        <Eventos />
      </Container>
      <Testimonio/>
    </div>
  );
};

export default FirstSection;
