import React from 'react';

import { Typography,Container,CardContent,Card, CardHeader,Grid, Avatar, CardMedia } from '@mui/material';
import { EventsData } from './EventsData';
import './FourthSection.css';
import MainTitle from '../Texts/MainTitle';

const MyEvents = () => {
    return (
      <Grid
        container
        rowSpacing={5}
        columnSpacing={3}
        alignItems="center"
        justifyContent="center"
        sx={{
          marginBottom: "4em",
        }}
      >
        {EventsData.map((data,id) => {
          console.log(EventsData)
          return (
            <React.StrictMode  key={id}>
              <Grid item xs={12} md={4}>
                <Card sx={{ maxWidth: '100%', background: "#1E1E1E" }}>
                  <CardHeader sx={{color:'white'}}
                    avatar={
                      <Avatar
                        sx={{ background: "var(--primary)" }}
                        aria-label="recipe"
                      >
                        {data.title.substring(0,2).toUpperCase()}
                      </Avatar>
                    }
                    title={data.title}
                    subheader={<Typography variant="caption" sx={{color:'#BABABA'}}>{data.date}</Typography>}
                  />
                  <CardMedia
                      component="img"
                      height="194"
                      loading="lazy"
                      image={data.img}
                      alt={data.title}
                  />
                  <CardContent>
                      <Typography variant="body2" color="#BABABA">{data.description}</Typography>
                  </CardContent>
                </Card>
              </Grid>
            </React.StrictMode>
          );
        })}
      </Grid>
    );
  };

const FourthSection = () => {
    return (
      <Container>
        <div className="fourth-section">
            <MainTitle
                textContent="Servicios"
                description="provident voluptatem perspiciatis, dolorum, et nihil facilis molestiae corrupti voluptate soluta tenetur officiis quaerat. Quis, rem voluptas"
             />
            <MyEvents />
        </div>
        </Container>
    );
};

export default FourthSection;